body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
    background: rgb(8, 8, 8);
    min-width: 100%;
}

@font-face {
    font-family: 'AlphaCentauri';
    src: local('AlphaCentauri'), url(assets/Alphacentauri-MVY4p.ttf) format('truetype');
}

html {
    scroll-behavior: smooth;
    min-width: 100%;
    overflow-x: hidden;
}